$background-color: #7185f6;
$toggle-button-size: 50px;
$offset: 30px;
$z-index: 10;
$menu-width: 300px;
$menu-height: 500px;

$images-dir: 'images' !default;

.ds-accessibility {
  direction: rtl;
  z-index: $z-index;
  min-width: 300px;
  user-select: none;
  position:fixed;
  bottom:30px;
  right:30px;

  .ds-accessibility-icon{
    display:inline-block;

    &--expand{
      background: url('images/expand.svg') no-repeat;
      width:40px; height: 50px;
      margin-top: 5px;
    }
    
    &--close{
      background: url('images/close.svg') no-repeat;
      width:50px; height: 50px;
    }
    
    &--zoom-out{
      background: url('images/zoom-out.svg') no-repeat;
      width:150px; height: 105px;
    }
    
    &--zoom-in{
      background: url('images/zoom-in.svg') no-repeat;
      width:150px; height: 105px;
    }
    
    &--invert{
      background: url('images/invert.svg') no-repeat;
      width:150px; height: 105px;
    }
    
    &--cursor{
      background: url('images/cursor.svg') no-repeat;
      width:150px; height: 105px;
    }
    
    &--brightness{
      background: url('images/brightness.svg') no-repeat;
      width:150px; height: 105px;
    }
    
    &--contrast{
      background: url('images/contrast.svg') no-repeat;
      width:150px; height: 105px;
    }
    
    &--monochrome{
      background: url('images/monochrome.svg') no-repeat;
      width:150px; height: 105px;
    }
    
    &--revert{
      background: url('images/revert.svg') no-repeat;
      width:150px; height: 105px;
    }
  }

  &.ds-accessibility-size-s {
    &.ds-accessibility-collapsed {
      .ds-accessibility-container {
        width: 40px;
        height: 40px;

        .ds-accessibility-expand-button {
          line-height: 40px;
          width: 40px;
          height: 40px;

          svg {
            width: 24px;
            height: 40px;
          }
        }        
      }    
    }
  }

  &.ds-accessibility-size-l {
    &.ds-accessibility-collapsed {
      .ds-accessibility-container {
        width: 160px;
        height: 160px;

        .ds-accessibility-expand-button {
          line-height: 160px;
          width: 160px;
          height: 80px;

          svg {
            width: 80px;
            height: 160px;
          }
        }        
      }    
    }
  }

  &-container {
    box-sizing: border-box;
    transition: transform 0.2s ease-in-out, width 0.3s ease-in-out, height 0.3s ease-in-out, background-color 0.3s ease-in-out, color 0.3s ease-in-out, border-radius 0.3s ease-in-out;
    box-shadow: rgba(0, 0, 0, 0.137255) 0px 0px 4px 0px, rgba(0, 0, 0, 0.278431) 0px 4px 8px 0px;
  }

  &-zoom {
    transition: zoom 0.3s ease-in-out;
  }

  &-cursor {
    cursor: url("images/cursor.png"), default;
    
    *:hover, * {
      cursor: url("images/cursor.png"), default;
    }

    &-workaround {
      pointer-events: none;
      position: absolute;
      width: 38px;
      height: 60px;
      z-index: 1000;
      background-image: url("images/cursor.png");
    }
  }

  &-highlighted-links {

    .highlighted-link {
      text-decoration: underline !important;
    }

    a,
    a:link,
    a:visited,
    a:hover,
    a:active {
      text-decoration: underline !important;
    }
  }

  $close-button-height: 40px;
  $close-button-width: 40px;
  &-close-button {
    box-sizing: border-box;
    width: $close-button-width;
    height: $close-button-height;
    margin-bottom:15px;
    cursor: pointer;

    img{
        width: $toggle-button-size;
    }
  }

  &-menu {
    -webkit-box-shadow: 0px 1px 8px 1px rgba(0,0,0,0.35);
    -moz-box-shadow: 0px 1px 8px 1px rgba(0,0,0,0.35);
    box-shadow: 0px 1px 8px 1px rgba(0,0,0,0.35);

    background-color: #fafcfd;
    color: rgb(11, 11, 11);
    width: $menu-width;
    height: $menu-height;

    $menu-button-height: 105px;
    $menu-button-width: 150px;

    &-button {
      float: right;
      box-sizing: border-box;
      width: $menu-button-width;
      height: $menu-button-height;
      cursor: pointer;
      transition: background-color 0.1s;
      text-align: center;
      position: relative;

      border-radius: 2px;
      border-bottom:3px solid #f2f2f2;

      &:hover {
        background-color: #f2f2f2;
        border-bottom:3px solid #000;
      }

      span {
        position: absolute;
        display: block;
        width: 100%;
        bottom: 10px;
        font-weight: bold;
        font-size: 17px;        
      }
    }

    &-footer {

      position: absolute;
      bottom: 0;
      width: 100%;
      height: 30px;
      line-height: 30px;
      text-align: center;
      font-family: Consolas, sans-serif;

      background-color: #f8f8f8;
      font-size: 10px;

      a {
        font-weight: bold;
        text-decoration: underline;

        &:link,
        &:visited,
        &:hover,
        &:active {
          color: #000000;
        }

      }
    }
  }

  &-expand-button {
    cursor: pointer;
    position: absolute;
    line-height: $toggle-button-size;
    width: $toggle-button-size;
    height: $toggle-button-size;
    text-align: center;
    background-color: $background-color;
    border-radius:50%;
    bottom:0;
    
    &:hover {
      transform: scale(1, 1) translate3d(0, 0, 0);
      box-shadow: rgba(0, 0, 0, 0.145098) 0px 0px 4.72988px 0px, rgba(0, 0, 0, 0.294118) 0px 4.72988px 9.45976px 0px;
    }

    img {
      fill: #fff;
      width: $toggle-button-size - 10;
      height: $toggle-button-size;
      line-height: $toggle-button-size;
      padding-top:1px;
    }
  }

  &.ds-accessibility-expanded {

    .ds-accessibility-container {
      width: $menu-width;
      height: $menu-height;
      border-radius: 2px;
      color: #333;
      background-color: #fff;
      transform: scale(1, 1) translate3d(0, 0, 0);
    }

  }

  /*
  &.ds-accessibility-collapsed {

    .ds-accessibility-container {
      width: $toggle-button-size;
      height: $toggle-button-size;
      border-radius: 50%;
      color: white;

      &:hover {
        transform: scale(1, 1) translate3d(0, 0, 0);
        box-shadow: rgba(0, 0, 0, 0.145098) 0px 0px 4.72988px 0px, rgba(0, 0, 0, 0.294118) 0px 4.72988px 9.45976px 0px;
      }
    }
  }
  */
}

@media screen and (max-width: 780px) {

  $horizontal-width: 25%;
  $horizontal-height: 10%;
  $vertical-width: 25%;
  $vertical-height: 25%;
  $close-button-height: 0px;
  $toggle-button-width:50px;
  $toggle-button-hight:55px;
  $toggle-button-icon:60px;
  $offset: 0px;
  $z-index: 10;
  $menu-width: 100%;
  $menu-height: 10%;
  .ds-accessibility
  {
    right: $offset;
    top: $offset;
    .ds-accessibility-menu
    {
      height: $horizontal-height;
      width: $menu-width;
    }
    $close-button-height: 0px;
    &.ds-accessibility-expanded {

      .ds-accessibility-container {
        height: $horizontal-height;
        width: $menu-width;
      }
    }

    &-menu {
      $menu-button-width: 110px;
      &-button {

        width: $horizontal-width;
        height: $horizontal-height;

      }
      &-footer {
        display: none !important;
      }
    }
    &-close-button {
      height: $close-button-height;

    }
    &-expand-button {
      cursor: pointer;
      position: absolute;
      line-height: $toggle-button-size;
      width: $toggle-button-width;
      height: $toggle-button-icon;
      text-align: center;

      svg {
        fill: #fff;
        width: 35px;
        height: $toggle-button-icon;
        line-height: $toggle-button-size;

      }
    }
    &.ds-accessibility-expanded {
      position: static;
    }
    &.ds-accessibility-collapsed {
      .ds-accessibility-container {
        width: $toggle-button-width;
        height: $toggle-button-hight;
        border-radius: 50% 0% 0% 50%;
        color: white;
        background-color: $background-color;
      }
    }
  }
}
